* {
  margin: 0;
  padding: 0;
  font-family: Lucida Sans, Lucida Sans Regular, Lucida Grande, Lucida Sans Unicode, Geneva, Verdana, sans-serif;
  font-weight: bolder;
}

:root {
  background-color: var(--sec);
  color: var(--primary);
  --sec: #f9fafb;
  --primary: #111827;
  --tertiary-color: #35d399;
  font-size: 2rem;
}

.header {
  z-index: 10;
  background-color: #f9fafbf2;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: fit-content;
  display: flex;
  position: fixed;
  top: 0;
  box-shadow: 0 1px 1px #0000001a;

  & .nav-items {
    justify-content: space-around;
    width: 50vw;
    display: flex;

    & img {
      border-radius: 50%;
      height: 10vh;
    }

    & .navList {
      margin: auto;
      list-style: outside;
      display: flex;

      & #ulCont {
        justify-content: space-between;
        display: flex;
      }

      & ul > li {
        justify-content: space-evenly;
        display: flex;

        & a {
          color: inherit;

          & li {
            margin: 10px;
            padding: 10px;
          }
        }
      }

      & li:hover {
        text-decoration: underline;
        text-decoration-color: var(--tertiary-color);
        text-decoration-style: solid;
        transition: all .2s;
      }
    }
  }

  & .connectButton {
    & button {
      border: .2rem solid var(--primary);
      font-size: inherit;
      background-color: #fff;
      border-radius: .5rem;
      padding: 5px;

      & a {
        color: var(--primary);
      }
    }
  }

  & .connectBtn:hover {
    background-color: var(--primary);
    transition: all .3s;

    & a {
      color: var(--sec);
    }
  }
}

a {
  text-decoration: none;
}

a:before, a:after {
  color: inherit;
}

.footer {
  text-align: center;
  background-color: #eaeaea;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  font-size: .7rem;
  display: flex;
  position: static;
  bottom: 0;

  & a {
    color: inherit;
    font-size: 1rem;
  }

  & a:after {
    color: inherit;
  }

  & .copyrightClaim, & .socials {
    margin: 10px;
  }

  & .socials {
    justify-content: space-evenly;
    align-items: center;
    width: 30vw;
    margin-top: 1.3rem;
    display: flex;
  }
}

.home {
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin: 5rem;
  display: flex;

  & .imgContainer {
    border: .4rem solid var(--primary);
    border-radius: 50%;
    width: fit-content;
    height: 50vh;
    margin: 1rem;
    overflow: hidden;

    & img {
      height: 100%;
    }
  }

  & .imgContainer:hover {
    border-color: var(--tertiary-color);
    transition: all .3s;
  }

  & .homeData {
    text-align: center;
    flex-direction: column;
    justify-content: space-evenly;
    height: 30vh;
    margin: 2rem;
    display: flex;

    & .name {
      color: var(--tertiary-color);
      font-size: 1.2rem;
    }

    & #heart {
      color: red;
      font-size: 1.5rem;
    }

    & #typedText {
      color: var(--tertiary-color);
    }
  }

  & .resumeBtn {
    border: .1rem solid var(--primary);
    color: var(--primary);
    width: 10vw;
    box-shadow: 0 0 100px var(--tertiary-color);
    border-radius: 10px;
    margin: 1rem;
    padding: 10px;
    font-size: .7rem;
  }

  & .resumeBtn:hover {
    background-color: var(--primary);
    color: #fff;
    transition: all .2s;
  }
}

.about {
  text-align: center;
  margin: 1rem;
  padding: 1rem;

  & .description {
    margin-top: 1rem;
    font-size: .7rem;
  }

  & h5 {
    text-align: center;
    text-shadow: 0 0 5px #0000001a;
    margin: 1rem 1rem 0;
  }

  & .educationInformation {
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    display: flex;

    & :hover {
      color: var(--sec);
      background-color: var(--primary);
    }

    & .diplomaEdu {
      border: 2px solid var(--primary);
      width: fit-content;
      box-shadow: 3px 3px 0 var(--tertiary-color);
      border-radius: 10px;
      margin: .5rem;
      padding: .3rem;

      & p {
        font-size: .5rem;
      }
    }

    & .degreeEdu {
      border: 2px solid var(--primary);
      width: fit-content;
      box-shadow: 3px 3px 0 var(--tertiary-color);
      border-radius: 10px;
      margin: .5rem;
      padding: .3rem;

      & p {
        font-size: .5rem;
      }
    }
  }
}

.project {
  text-align: center;
  background-color: var(--primary);
  color: var(--primary);
  border-radius: 20px;
  margin: 1rem;
  padding: 1rem;

  & h3 {
    color: var(--tertiary-color);
  }

  & .projectCardsContainer {
    flex-wrap: wrap;
    justify-content: center;
    display: flex;

    & .project-card {
      background-color: var(--tertiary-color);
      border-radius: 10px;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 20vw;
      margin: 1rem;
      padding: 10px;
      display: flex;
      box-shadow: 4px 4px 6px #ffdf644d;

      & img {
        border-radius: 10px;
        width: 100%;
      }

      & h2 {
        font-size: .8rem;
      }

      & p {
        font-size: .4rem;
        font-weight: 100;
      }

      & a {
        & button {
          background-color: var(--sec);
          border: 1px solid var(--primary);
          border-radius: 5px;
          padding: 5px;
        }

        & button:hover {
          color: var(--sec);
          background-color: var(--primary);
          transition: all .2s;
        }
      }

      & .techStack {
        & .tech-item {
          background-color: #ffdf64;
          border-radius: 5px;
          margin: .1rem;
          padding: .1rem;
          font-size: .4rem;
        }
      }
    }

    & .project-card:hover {
      transition: all .5s;
      transform: scale(1.1);
    }
  }
}
